(function () {
    const $ = function (el) {
        if (Array.isArray(el)) {
            return document.querySelectorAll(el);
        }

        return document.querySelector(el);
    };

    const menuBurger = $(".header__nav-menu"),

        menuList = menuBurger.querySelector(".header__nav-list");

    

    menuBurger.addEventListener('click', () => {

        menuBurger.classList.add('show');

    })

    

    document.addEventListener("click", (e) => {

        let target = e.target;

    

        if (

            menuBurger.classList.contains("show") &&

            target != menuBurger &&

            target != menuList &&

            target.className != "header__nav-link"

        )

            menuBurger.classList.remove("show");

    });

    
    const preview = $([".products__item-preview"]);
    
    preview.forEach((parent) => {
        let previewLabels = parent.querySelectorAll("label");
    
        previewLabels.forEach((button) => {
            button.addEventListener("click", () => {
                let previewImg = button.querySelector("img"),
                    mainImg = parent.parentNode.querySelector(
                        ".products__item-pic"
                    );
    
                mainImg.setAttribute("src", previewImg.getAttribute("src"));
            });
        });
    });
    
    const inputs = $(['.panel__add-descr input']),
        files = $(['.panel__add-pictures input']);
    
    document.addEventListener("click", (e) => {
        const target = e.target;
    
        inputs.forEach(input => {
            const label = input.nextElementSibling;
    
            if (input == target || input.value) {
                label.classList.add("active");
            } else {
                label.classList.remove("active");
            }
        });
    
        files.forEach((input => {
            input.addEventListener("change", () => {
                const label = input.nextElementSibling,
                    div = label.querySelector("div:first-of-type");
    
                if (input.files && input.files.length >= 1) {
                    div.innerHTML = "Выбрано: " + input.files[0].name;
                }
            })
        }))
    });
    
    
    // inputs.forEach(input => {
    
    // })
})();

