const menuBurger = $(".header__nav-menu"),
    menuList = menuBurger.querySelector(".header__nav-list");

menuBurger.addEventListener('click', () => {
    menuBurger.classList.add('show');
})

document.addEventListener("click", (e) => {
    let target = e.target;

    if (
        menuBurger.classList.contains("show") &&
        target != menuBurger &&
        target != menuList &&
        target.className != "header__nav-link"
    )
        menuBurger.classList.remove("show");
});
